.attachmentContainer {
  label {
    font-weight: $fontWeightSemiBold;
    font-size: $fontSizeStandard;
  }
  .attachmentInner {
    border-radius: 0;
    border: 1px solid #bcbcbc;
  
  
    margin-top: 15px;  
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-wrap: wrap;
    
    ul {
      margin: 0;
      padding: 5px 0 0 0;
      flex: 1 0 100%;
      border-bottom: 1px solid #bcbcbc;
      height: 100px;
      overflow-y: auto;
      overflow-x: hidden;
  
      li {
        text-align: left;

        

        display: flex;
      }
    }
  }

  li {
    &.attachmentList {
   
      padding: 5px 7px 5px 13px;

      label {
        font-size: 14px;
        font-weight: 400;
        color: #333 !important;
        line-height: 1;
        vertical-align: top;
        margin-bottom: 0 !important;
        flex: 1 1 100%;
        margin-left: 6px;        
        padding: 1px 3px;
      }

      i {
        height: 18px;
        color: #333;
        width: 18px;
        font-size: 10px;       
        font-weight: 600;
    
      }

      button {
        background: none;
        text-align: right;
        border: 0;
        cursor: pointer;
        margin: 0;
        font-size: $fontSizeH3;
        line-height: 1;
        text-transform: uppercase;
        font-weight: $fontWeightSemiBold;
        i{
          height: auto;
          width: auto;
        }
      }
    }
  }

  .browseUi {
    i {
      font-size: 16px;
      color:#767676;
    }
    span {
      color: #6b6868;
      font-size: $fontSizeSmall;

      &:hover{
     
        span{
          text-decoration: underline;
        }
      }
    }
  }
}
