@media screen and (-ms-high-contrast: active) {
  .feedbackpageTab {
    [role="tablist"] {
      border-bottom: 1px solid;
    }
    button {
      background-color: none !important;
      &:hover {
        background-color: none !important;
      }
      &:active {
        background-color: none !important;
      }
    }
  }
  .customDropdown{
  
    ul{
      border:1px solid;
    }
  }
.feedbackContainer{

  .selectFilter{
    .searchBox{
      border: 1px solid;
      > div{
        border: 1px solid;
      }
    }
  }
  .tabsButtons{
    border-top:1px solid;
    border-bottom:1px solid;
  button{
    border:1px solid;
  }
  .tabOptionButton{
    border:1 solid;
    ul{
      border: 1px solid;
      margin-top: 1px solid;

    }
  }
  > span{
    border:1px solid !important; 
  }
}
.existingTabcontainer{
  .rightContent{
    border:1px solid;
  }
}
}
}
@media screen and (-ms-high-contrast: black-on-white) {
