.react-checkbox-tree {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-ms-flex-direction: row;
	flex-direction: row;
	font-size: 16px;
	margin-top: 6px;
	overflow-x: hidden;
	height: 100vh;
	overflow-y: auto;
}
.rct-icon {
	width: 0;
}
.selectFilter label {
	font-weight: 600;
	font-size: 14px;
	margin-bottom: 15px;
	display: inline-block;
}
.selectFilter label + div > i {
	font-size: 13px;
	color: #2a76d4;
	cursor: pointer;
	font-weight: 600;
	vertical-align: middle;
	margin-left: 5px;
	cursor: pointer;
}
.rct-collapse.rct-collapse-btn + label span.rct-title {
	font-weight: 400;
	font-size: 14px;
	color: #333;
}
.react-checkbox-tree > ol > li span.rct-title {
	font-weight: 600;
	font-size: 13px;
	color: #333;
}
.react-checkbox-tree > ol > li > ol >li span.rct-title {
	font-weight: 400;
	font-size: 12px;
	color: #333;
	vertical-align: top;	
    line-height: 22PX;
}
.react-checkbox-tree > ol > li > ol >li > span .rct-title{
	font-weight: 600;
	font-size: 13px;
	color: #333;
}
.parentClose,
.parentOpen {
	display: none !important;
}
.react-checkbox-tree > ol {
	-webkit-box-flex: 1;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
}
.react-checkbox-tree ol {
	margin: 0;
	padding-left: 0;
	list-style-type: none;
	padding-top: 4px;
}
.rct-node-parent .rct-bare-label .rct-node-icon {
	float: right;
	height: 20px;
	width: 20px;
	padding: 0;
	margin-top: 6px;
}
.rct-node-parent .rct-bare-label .rct-node-icon i {
	font-size: 13px;
	color: #2a76d4;
	width: 13px;
	cursor: pointer;
	font-weight: 400;
	height: 13px;
}
.rct-node-parent .rct-bare-label .rct-node-icon i:focus{
outline: 1px dotted #333;
}
.react-checkbox-tree ol li span.rct-title {
	text-overflow: ellipsis;
	overflow: hidden;
	width: 100%;
	white-space: nowrap;
}
.react-checkbox-tree ol li label {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
	width: 293px;
}
.react-checkbox-tree ol ol {
	padding-left: 0;
}
.react-checkbox-tree button {
	line-height: normal;
	color: inherit;
}
.react-checkbox-tree button i {
  line-height: 15px;
  margin-top: -3px;
}
.react-checkbox-tree button:focus {
	outline: none;
}
.react-checkbox-tree button:focus i {
  outline: 1px dotted;
  line-height: 15px;
  margin-top: -3px;
}
.react-checkbox-tree button:disabled {
	cursor: not-allowed;
}
.react-checkbox-tree .rct-bare-label {
	cursor: default;
}
.react-checkbox-tree label {
	margin-bottom: 0;
	cursor: pointer;
}
.react-checkbox-tree:not(.rct-native-display) input {
	display: none;
}
.react-checkbox-tree.rct-native-display input[type="checkbox"] {
	display: none;
}
.react-checkbox-tree.rct-native-display input {
	margin: 0 5px;
}

li.rct-node.rct-node-leaf.rct-disabled .rct-checkbox [tabindex="0"]:focus{
	outline: none;
}
.rct-node.rct-node-parent.rct-disabled .rct-checkbox i:focus{
	outline: none;
}
.rct-disabled > .rct-text > label {
	opacity: .75;
	cursor: default;
}
.rct-disabled > .rct-text > label:hover {
	background: transparent;
}
.rct-disabled > .rct-text > label:active {
	background: transparent;
}
.rct-text {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
}
.rct-options {
	-webkit-box-flex: 0;
	-ms-flex: 0 0 auto;
	flex: 0 0 auto;
	margin-left: .5rem;
	text-align: right;
}
.rct-option {
	opacity: .75;
	border: 0;
	background: none;
	cursor: pointer;
	padding: 0 4px;
	font-size: 18px;
}
.rct-option:hover {
	opacity: 1;
}
span.rct-node-icon {
	vertical-align: middle;
}
.react-checkbox-tree > ol > li > ol >li > span .rct-node-icon {
	display: none
}
.rct-option + .rct-option {
	margin-left: 2px;
}
.rct-collapse,
.rct-checkbox,
.rct-node-icon {
	padding: 0 5px;
}
.rct-collapse *,
.rct-checkbox *,
.rct-node-icon * {
	display: inline-block;
	margin: 0;
	width: 19px;
	vertical-align: top;
	height: 19px;
}
.rct-collapse {
	border: 0;
	background: none;
	line-height: normal;
	color: inherit;
	font-size: 12px;
}
.rct-collapse.rct-collapse-btn {
	cursor: pointer;
}
.rct-collapse > .rct-icon-expand-close {
	opacity: .5;
}
.rct-collapse > .rct-icon-expand-close:hover {
	opacity: 1;
}
.rct-native-display .rct-checkbox {
	display: block;
	float: left;
	padding-top: 3px;
	border-radius: 100%;
}
.rct-checkbox i.ms-CheckboxCompositeReversed {
	color: #53AF95;
	border-radius: 100%;
}
.rct-checkbox i.ms-Checkbox {
	color: #BCBCBC;
	border-radius: 100%;
}
.rct-checkbox i:focus {
  outline: 1px dotted #333;
}
.rct-collapse-btn i {
	font-size: 11px;
	margin-top: 3px;
}
.rct-node-clickable {
	cursor: pointer;
}
.rct-node-clickable:hover {
	background: rgba(51, 51, 204, 0.1);
}
.rct-node-clickable:focus {
	outline: 0;
	background: rgba(51, 51, 204, 0.2);
}
.rct-title {
	padding: 0 5px;
}
@media screen and (min-width:699px) and (max-width:768px){

		
	.react-checkbox-tree ol li label{
	  
		width: 225px;
	  }
	  
}

@media screen and (max-width:698px){

	
	.react-checkbox-tree ol li label{
	  
	  width: 175px;
	}
	
	}