.fullPageCenterSpinner{
  height: 100%;
  display: flex;
  width: 100%;
  justify-content: center;
  top: 0;
  z-index: 999;
  left: 0;
  position: absolute;
  background: rgba(234, 233, 233,.95);
}
.pageCenterSpinner{
      
  height: 100%;
  display:flex;
  width: 100%;
  justify-content: center;    
  left: 0;
  position: absolute;
}
.errorMsg{
  font-size: 16px;
  color:#a4262c;  
  margin-left: 2px;
}

