.engagementDialogFooterButton {

  button {
    margin-left: 20px;
    
  }
}


.engagementIpContr {

  .msgClose{

    button{
      background: none;
      border:none;
    }
  
    i{
      font-size:12px;
      cursor: pointer;    
    }
  
  }
  padding: 20px !important;
  label{
    &.subTitle{
      font-size: $fontSizeSmall;
      color: #333333;
      font-weight: 600;
    }
  }
  h4 {
    &.subTitle {
      font-size: $fontSizeSmall;
      color: $neutralPrimary;
      line-height: 1;
      font-weight: 600;
      margin: 0;
      i {
        font-weight: 900;
        margin: 0px 0 0 8px;
        vertical-align: middle;
        cursor: pointer;
        line-height: 1;
        font-size: 13px;
      }
    }
  }

  &.startUphome,
  &.closeOut {
    width: 840px;     
    margin: 0px 20px 20px 20px;
    padding-top: 20px !important;
  }
  
}
.surveyFormContr {
  border: 1px solid transparent;
}
.formFields {
  label {
    &.subTitle {
      font-size: $fontSizeSmall;
      color: $neutralPrimary;
      line-height: 1;
      margin: 0;
      i {
        font-weight: 900;
        margin: 0px 0 0 8px;
        vertical-align: middle;
        cursor: pointer;
        line-height: 1;
        font-size: 13px;
      }
    }
  }

  :global {
    .ms-TextField {
      margin-top: 10px;
      margin-bottom: 10px;
    }
    .ms-TextField-fieldGroup {
      margin-top: 5px;
      margin-bottom: 5px;
    }
  }
}
.nextCancelBtns{
  margin-top: 50px;
  
  button{
    margin-right: 20px;
  }
}
.resourcesLink {
  margin: 20px;
  h3 {
    margin: 10px 0;
    font-weight: 600;
    font-size: 14px;
  }
  b{
    margin: 5px 0;
    display: inline-block;
    font-size: 14px;
    font-weight: 600;

  }
  .resrcLinkwithOutBorder {
    flex-flow: row wrap;
    display: flex;
    
    width: 800px;

  }
  .resrcLinkwithBorder {
    border: 1px solid #d8d8d8;
    border-radius: 5px;
    flex-flow: row wrap;
    display: flex;
    
    width: 800px;
    

    padding: 10px;
    .resrcLinkwithChild{
      margin: 0 10px;
      padding: 0;
      text-align: left;
      width: 239px;
      margin-bottom: 15px;
      ul {
      margin:0;
      padding: 0;
      }
    }
    
  }
  ul {
    padding: 0;
    margin:0;

    
    li {
      list-style: none;
      
    margin-bottom: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
      a {
        text-decoration: underline;
        color: #3b8072;
        font-size: 14px;
        cursor: pointer;
      }
    }
  }
}
