@media screen and (min-width: 699px) and (max-width: 900px) {
  .engagementIpContr {
    &.startUphome,
    &.closeOut {
      width: 100%;
    }
  }
  .resourcesLink{
    .resrcLinkwithBorder{
width:100%;
.resrcLinkwithChild{
  width:45%;
}
    }
    .resrcLinkwithOutBorder{
      width:100%;
    }
  }
}

@media screen and (min-width: 499px) and (max-width: 698px) {
  .engagementIpContr {
    &.startUphome,
    &.closeOut {
      width: 100%;
      
    margin: 210px 20px 20px 0px;
    
    }
   
  } 

  .resourcesLink{
    .resrcLinkwithBorder{
width:100%;

.resrcLinkwithChild{
  width:45%;
}
    }
    .resrcLinkwithOutBorder{
      width:100%;
    
    }
  }
}
@media screen and (min-width: 299px) and (max-width: 498px) {
  .engagementIpContr {
    padding: 10px;
    &.startUphome,
    &.closeOut {
      width: 100%;
      
    margin: 210px 20px 20px 0px;
    
    }
    .surveyBoxContainer{
      width: 100%;
    }
  }
  .surveyBoxContainer{
    width: 100%;
  }
  .resourcesLink{
    .resrcLinkwithBorder{
width:100%;

.resrcLinkwithChild{
  width:100%;
}
    }
    .resrcLinkwithOutBorder{
      width:100%;
    
    }
  }
}
