
*, *:before, *:after {
    box-sizing: border-box;
  }
  .cancelIpFeedback{
      margin: 0 20px 20px 20px;
      width: 840px;   
  }
  .bottomContent {
    text-align: right;
    margin: 20px 0 20px 0;
    
    justify-content: flex-end;
    &.btnRightAlign{
      margin-right: 20px;
    }
  
    button {
      margin-left: 20px !important;
      
    }
  }
  .pageCenterSpinner{
      
    height: 100%;
    display: flex;
    width: 100%;
    justify-content: center;
    top: 0;
    z-index: 999;
    left: 0;
    position: absolute;
    background: rgba(234, 233, 233,.95);
  }
.surveyBoxContainer{
    padding: 20px;    
    background-color: #FBFBFB;
    border:1px solid #D8D8D8;
    border-radius: 5px;    
    display: flex;
    min-height: 100px;
    width: 840px;        
    margin: 20px 0px 20px 0;
    align-items: center;
 
    &.engagementStartupSection{
        flex-basis: 100%;        
        flex-wrap: wrap;

        b{
            color: #000000;
        }

        a{
            color:#0c76c7;
        }
    }
    span{
        &.surveyMsg{
            margin: 0 13px;
            font-size: 14px;
        }
    
        b{
            color: rgb(164, 38, 44);
        }
    }

    .campusSearchLink{
        display: flex;
        width: 55%;
        padding: 20px 0;
        margin: 0 13px;

        a{
            color:#0c76c7;
            font-weight: 600;
        }
    }
    .surveyRightPart{
        margin-left: auto;
       
     div {
 [role="radiogroup"] {

    span{
        margin: 0 30px 0 0;
        font-size: 14px;        
        
    }
    
}
     } 
        :global{
            .ms-ChoiceFieldGroup-flexContainer{
                display: flex;
                font-size: 14px;
                > div{
                    margin: 0;
                    &:last-child{
                        span{
                            margin-right: 0;
                        }
                    }
                }
            }
            .ms-BasePicker {
                width:270px;
                height: 30px;

              
            }
        }
    }

}

@media screen and (min-width: 499px) and (max-width: 898px) {
.cancelIpFeedback{
    width: 100%;
    margin: 0 0 0 0;
    padding: 20px;
}
    .surveyBoxContainer{
        flex-basis: 100%;
        width: 100%;
        flex-wrap: wrap;
        margin-top: 0;
        .surveyRightPart{
            margin: 0;
            width: 100% !important;
            div{
                div{
                    margin: 0;
                    width: 100% !important;
                }
            }
        }
        .surveyMsg{
            
            margin: 0 0 10px 0 !important;
        }
      }
}
@media screen and (min-width: 320px) and (max-width: 498px) {
    .cancelIpFeedback{
        width: 100%;
        margin: 0 0 0 0;
        padding: 20px;
    }
    .surveyBoxContainer{
        width: 100%;
        flex-basis: 100%;
        flex-wrap: wrap;
        margin-top: 0;
        .surveyRightPart{
            margin: 0;
            width: 100% !important;
            div{
                div{
                  
                    width: 100% !important;
                }
            }
        }
        .surveyMsg{
            width: 100%;
            margin: 0 0 10px 0 !important;
         
           
        }
      }

}